.modal-background {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 100;
  animation-name: fadeIn;
  animation-duration: 0.5s;
}

.modal-input-group {
  font-size: 1em;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 400px;
  min-width: 160px;
  background-color: white;
  padding: 20px;
  position: fixed;
  top: calc((50vh / 2) + 20px);
  left: calc(50vw - (440px / 2));
  z-index: 200;
  opacity: 1;
  box-shadow: 0 0 50px 0px black;
  color: rgba(86, 86, 86, 1);
  @media screen and(max-width: 500px) {
    width: 220px;
    top: calc((50vh / 2) + 20px);
    left: calc(50vw - (220px / 2) - 20px);
    font-size: 0.8em;
  }
  p {
    margin: 10px auto;
    width: 90%;
    padding: 5px;
    text-align: center;
    animation-name: errorFadeIn;
    animation-duration: 0.5s;
    color: red;
  }
  & h3 {
    color: #456565;
    padding: 10px 0;
    margin: 0;
    border-bottom: 1px solid #ececec;
  }
  // .modal-memo {
    
  // }
  .modal-input {
    padding: 10px 30px;
    height: 60px;
    // .is-required{
    // 	color:red;
    // 	font-size: 0.8em;
    // 	margin-left:20px;
    // }
    & input {
      width: 98%;
      font-size: 1.3em;
      border: none;
      background-color: transparent;
      border-bottom: 1px solid rgba(0, 157, 208, 0.2);
      // color: rgba(86, 86, 86, 1);
      &::placeholder {
        color: rgba(222, 222, 222, 1);
      }
      &:focus {
        outline: none;
        border-bottom: 2px solid rgba(0, 157, 208, 1);
        animation-name: inputBorderFadeIn;
        animation-duration: 0.2s;
        animation-timing-function: linear;
      }
      &.error {
        box-shadow: 0 0 3px 2px rgba(255, 0, 0, 0.8);
      }
    }
    label {
      color: rgba(6, 6, 6, 0.62);
      font-size: 1em;
    }
  }
  .modal-memo{
  	$marginLeft: 20px;
  	label{
  		margin-left: $marginLeft;
  		// .is-required{
	   //  	color:red;
	   //  	font-size: 0.8em;
	   //  	margin-left:20px;
	   //  }
  	}
  	textarea{
  		$padding: 5px;
  		font-size: 1.3em;
		  resize: none;
		  width: calc(100% - #{$marginLeft} * 2 - #{$padding} * 2);;
		  margin: 5px $marginLeft;
		  padding: $padding;
		  height: 100px;
		  border: 1px solid rgba(173, 216, 230, 1);
		  &::placeholder {
		    color: rgba(222, 222, 222, 1);
		  }
			&.error {
			  box-shadow: 0 0 3px 2px rgba(255, 0, 0, 0.8);
			}
  	}
  }
  .radio-group {
    padding: 0 10px;
    margin: 10px 20px;
    &.error {
      box-shadow: 0 0 3px 2px rgba(255, 0, 0, 0.8);
    }
    p{
    	padding:0;
    	margin:0;
    }
  }
  .modal-input-footer {
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-gap: 10px;
  }
  & h3 {
    text-align: center;
    font-size: 1.8em;
  }
  & button {
    background-color: lightblue;
    height: 2em;
    outline-style: revert;
    outline-width: thin;
    border-width: 0;
    font-size: 1.1em;
    color: #025b90;
    &:hover {
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    }
  }
  animation-name: zoomIn;
  animation-duration: 0.5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes zoomIn {
  0% {
    transform: scale(0.3);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes inputBorderFadeIn {
  0% {
    border-bottom: 1px solid rgba(0, 157, 208, 0.2);
  }
  100% {
    border-bottom: 2px solid rgba(0, 157, 208, 1);
  }
}
@keyframes errorFadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
