@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&family=Roboto&display=swap);
div{font-family:'Roboto', sans-serif}body{margin:0;padding:0;font-size:16px;overflow:hidden;box-sizing:border-box}#root .container{display:grid;background-color:white;grid-template-areas:'title header header header' 'side main main main';grid-template-rows:10vh 90vh;grid-template-columns:minmax(280px, 1fr) 3fr;grid-gap:1px;min-height:-webkit-fill-available}#root .container .back-font{display:none}#root .container .cg-title h3{grid-area:title;text-align:center}#root .container .cg-side-button{display:none}#root .container .cg-side-button .arrow{border-top:15px solid transparent;border-bottom:15px solid transparent;border-left:15px solid #3a3a3a;border-right:15px solid transparent;width:0;height:0;margin-left:5px}#root .container .cg-side-button .arrow.rotate{border-left:15px solid transparent;border-right:15px solid #3a3a3a;margin-left:-10px}#root .container .cg-container{background-color:#ccdade;grid-area:side;display:grid;grid-template-rows:10fr 70px;grid-template-columns:minmax(250px, 100%)}#root .container .cg-container .cg-list{overflow-y:auto}#root .container .cg-container .cg-list .cg-item{margin:10px;border:1px solid whitesmoke;background-color:#cff1ff;padding:15px 4px;border-radius:5px;display:grid;grid-template-columns:4fr 1fr;grid-template-rows:1fr}#root .container .cg-container .cg-list .cg-item.active{border:4px solid #70a0d0;background-color:#95c0d6}#root .container .cg-container .cg-list .cg-item p{margin:auto 0;overflow:hidden}#root .container .cg-container .cg-list .cg-item span{cursor:auto}#root .container .cg-container .cg-list .cg-item span img{margin-left:3px}#root .container .cg-container .cg-list .cg-item span img:hover{transform:scale(1.2)}#root .container .cg-container .cg-button{padding:7px}#root .container .cg-container .cg-button button{display:flex;justify-content:center;align-items:center;z-index:50;height:100%;width:100%;border-radius:5px;background:#3360ff;color:white;outline-style:revert;outline-width:thin;border-width:0;font-size:1.2em;transition:0.3s}#root .container .cg-container .cg-button button:hover{box-shadow:0px 0px 9px 2px #009aec}#root .container .cg-container .cg-button button:active{background-color:#1b458c}#root .container .task-title{padding:10px;margin:auto 40px;overflow:hidden;grid-area:header;background-color:#fafafa}#root .container .task-list{padding:10px 0;grid-area:main;background-color:#eff8fb;display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, auto));grid-auto-rows:300px;justify-content:center;overflow-y:auto}#root .container .task-list .task-add-button{position:fixed;z-index:5;bottom:50px;right:50px;width:65px;height:65px;text-align:center;font-size:3.3em;color:white;border-radius:50%;background-color:#3360ff;box-shadow:0 0 10px 0px #3360ff;opacity:0.3;cursor:pointer}#root .container .task-list .task-add-button:hover{transition:0.3s;opacity:1}#root .container .task-list .task-item{position:relative;height:auto;border:1px solid #e4e4e4;background-color:rgba(255,253,204,0.82);border-radius:5px 20px 5px 5px;color:gray;display:grid;grid-template-rows:1fr 6fr 1fr 1fr;grid-template-columns:minmax(250px, 350px);grid-gap:1px;align-items:center;box-shadow:0 0 10px 0px #d8d8d8;margin:10px;padding:0.7em;cursor:pointer;-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:0.5s;animation-duration:0.5s}#root .container .task-list .task-item.urgent::after,#root .container .task-list .task-item.high::after,#root .container .task-list .task-item.normal::after{position:absolute;content:'';width:30px;height:30px;border-radius:50%;right:10px;bottom:10px}#root .container .task-list .task-item.urgent::after{background-color:#ff5e52}#root .container .task-list .task-item.high::after{background-color:#f9e52c}#root .container .task-list .task-item.normal::after{background-color:#f3f3f0}#root .container .task-list .task-item p{padding:0;margin:0}#root .container .task-list .task-item p:nth-child(1){color:#0f86b3;font-size:1.2em;overflow:hidden}#root .container .task-list .task-item p:nth-child(2){overflow-y:auto;font-size:0.9em;justify-self:center}#root .container .task-list .task-item p:nth-child(3){font-size:0.7em;border-top:1px solid #e4e4e4;padding-top:10px;font-style:italic}#root .container .task-list .task-item p:nth-child(4){font-size:0.7em;font-style:italic}#root .container .task-list .task-item.done{background-color:white;opacity:0.3}#root .container .task-list .task-item.done p:nth-child(1){-webkit-text-decoration-line:line-through;text-decoration-line:line-through;color:gray}#root .container .task-list .task-item.done p:nth-child(2){-webkit-text-decoration-line:line-through;text-decoration-line:line-through;color:gray}#root .container .task-list .task-item.done::before{position:absolute;content:'';left:calc(50% - 80px / 2);top:calc(50% - 40px / 2 - 50px);width:80px;height:40px;border-left:15px solid green;border-bottom:15px solid green;background-color:transparent;transform:rotate(-45deg);border-radius:10px;-webkit-animation-name:ticRotate;animation-name:ticRotate;-webkit-animation-duration:0.5s;animation-duration:0.5s}#root .container .task-list .task-item.only-title{grid-template-rows:7fr 1fr 1fr}#root .container .task-list .task-item.only-title p:nth-child(1){color:#0f86b3;font-size:1.2em;overflow:hidden;justify-self:center}#root .container .task-list .task-item.only-title p:nth-child(2){font-size:0.7em;border-top:1px solid #e4e4e4;padding-top:10px;font-style:italic;justify-self:start}#root .container .task-list .task-item.only-title p:nth-child(3){font-size:0.7em;font-style:italic;border:none}#root .container .task-list .task-item.only-title.done p:nth-child(1){color:gray}#root .container .task-list .task-item.only-title.done p:nth-child(2){-webkit-text-decoration-line:none;text-decoration-line:none}@media screen and (max-width: 560px){#root .container{grid-template-areas:'header' 'main';grid-template-columns:1fr;grid-template-rows:10vh 90vh}#root .container .cg-side-button{display:flex;align-items:center;position:fixed;top:50vh;left:0;width:25px;height:80px;background-color:rgba(228,241,241,0.4);border-radius:0 20px 20px 0;border:1px solid #9a9797;z-index:80;transition:0.5s}#root .container .cg-side-button:hover{box-shadow:0 0 20px black}#root .container .back-font{display:block;position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:rgba(0,0,0,0.7);z-index:10}#root .container .back-font.fadeout{transform:translate(-100vw);opacity:0;-webkit-animation-name:fadeOut;animation-name:fadeOut;-webkit-animation-duration:0.3s;animation-duration:0.3s}#root .container .back-font.fadein{opacity:1;-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:0.5s;animation-duration:0.5s}#root .container .cg-title{display:none}#root .container .cg-container{position:fixed;top:0;left:0;width:300px;height:100vh;z-index:50}#root .container .cg-container.show{-webkit-animation-name:slideIn;animation-name:slideIn;-webkit-animation-duration:0.3s;animation-duration:0.3s}#root .container .cg-container.hide{transform:translateX(-300px);opacity:0;-webkit-animation-name:slideOut;animation-name:slideOut;-webkit-animation-duration:0.3s;animation-duration:0.3s}}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeOut{0%{opacity:1;transform:translate(0)}100%{opacity:0;transform:translate(0)}}@keyframes fadeOut{0%{opacity:1;transform:translate(0)}100%{opacity:0;transform:translate(0)}}@-webkit-keyframes slideIn{0%{transform:translateX(-300px);opacity:0}100%{opacity:1}}@keyframes slideIn{0%{transform:translateX(-300px);opacity:0}100%{opacity:1}}@-webkit-keyframes slideOut{0%{transform:translateX(0);opacity:1}100%{transform:translateX(-300px);opacity:1}}@keyframes slideOut{0%{transform:translateX(0);opacity:1}100%{transform:translateX(-300px);opacity:1}}@-webkit-keyframes ticRotate{0%{transform:rotate(30deg) scale(0.5);opacity:0}100%{transform:rotate(-45deg) scale(1);opacity:1}}@keyframes ticRotate{0%{transform:rotate(30deg) scale(0.5);opacity:0}100%{transform:rotate(-45deg) scale(1);opacity:1}}

.modal-background{position:fixed;width:100%;height:100%;left:0;top:0;background-color:rgba(0,0,0,0.7);z-index:100;-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:0.5s;animation-duration:0.5s}.modal-input-group{font-size:1em;display:flex;justify-content:center;flex-direction:column;width:400px;min-width:160px;background-color:white;padding:20px;position:fixed;top:calc((50vh / 2) + 20px);left:calc(50vw - (440px / 2));z-index:200;opacity:1;box-shadow:0 0 50px 0px black;color:#565656;-webkit-animation-name:zoomIn;animation-name:zoomIn;-webkit-animation-duration:0.5s;animation-duration:0.5s}@media screen and (max-width: 500px){.modal-input-group{width:220px;top:calc((50vh / 2) + 20px);left:calc(50vw - (220px / 2) - 20px);font-size:0.8em}}.modal-input-group p{margin:10px auto;width:90%;padding:5px;text-align:center;-webkit-animation-name:errorFadeIn;animation-name:errorFadeIn;-webkit-animation-duration:0.5s;animation-duration:0.5s;color:red}.modal-input-group h3{color:#456565;padding:10px 0;margin:0;border-bottom:1px solid #ececec}.modal-input-group .modal-input{padding:10px 30px;height:60px}.modal-input-group .modal-input input{width:98%;font-size:1.3em;border:none;background-color:transparent;border-bottom:1px solid rgba(0,157,208,0.2)}.modal-input-group .modal-input input::-webkit-input-placeholder{color:#dedede}.modal-input-group .modal-input input:-ms-input-placeholder{color:#dedede}.modal-input-group .modal-input input::-ms-input-placeholder{color:#dedede}.modal-input-group .modal-input input::placeholder{color:#dedede}.modal-input-group .modal-input input:focus{outline:none;border-bottom:2px solid #009dd0;-webkit-animation-name:inputBorderFadeIn;animation-name:inputBorderFadeIn;-webkit-animation-duration:0.2s;animation-duration:0.2s;-webkit-animation-timing-function:linear;animation-timing-function:linear}.modal-input-group .modal-input input.error{box-shadow:0 0 3px 2px rgba(255,0,0,0.8)}.modal-input-group .modal-input label{color:rgba(6,6,6,0.62);font-size:1em}.modal-input-group .modal-memo label{margin-left:20px}.modal-input-group .modal-memo textarea{font-size:1.3em;resize:none;width:calc(100% - 20px * 2 - 5px * 2);margin:5px 20px;padding:5px;height:100px;border:1px solid #add8e6}.modal-input-group .modal-memo textarea::-webkit-input-placeholder{color:#dedede}.modal-input-group .modal-memo textarea:-ms-input-placeholder{color:#dedede}.modal-input-group .modal-memo textarea::-ms-input-placeholder{color:#dedede}.modal-input-group .modal-memo textarea::placeholder{color:#dedede}.modal-input-group .modal-memo textarea.error{box-shadow:0 0 3px 2px rgba(255,0,0,0.8)}.modal-input-group .radio-group{padding:0 10px;margin:10px 20px}.modal-input-group .radio-group.error{box-shadow:0 0 3px 2px rgba(255,0,0,0.8)}.modal-input-group .radio-group p{padding:0;margin:0}.modal-input-group .modal-input-footer{margin-top:20px;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr;grid-gap:10px}.modal-input-group h3{text-align:center;font-size:1.8em}.modal-input-group button{background-color:lightblue;height:2em;outline-style:revert;outline-width:thin;border-width:0;font-size:1.1em;color:#025b90}.modal-input-group button:hover{box-shadow:0 0 5px rgba(0,0,0,0.5)}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes zoomIn{0%{transform:scale(0.3);opacity:0}100%{transform:scale(1);opacity:1}}@keyframes zoomIn{0%{transform:scale(0.3);opacity:0}100%{transform:scale(1);opacity:1}}@-webkit-keyframes inputBorderFadeIn{0%{border-bottom:1px solid rgba(0,157,208,0.2)}100%{border-bottom:2px solid #009dd0}}@keyframes inputBorderFadeIn{0%{border-bottom:1px solid rgba(0,157,208,0.2)}100%{border-bottom:2px solid #009dd0}}@-webkit-keyframes errorFadeIn{0%{opacity:0}100%{opacity:1}}@keyframes errorFadeIn{0%{opacity:0}100%{opacity:1}}

